/** Shared Styles */
.header {
  font-family: inherit;
  line-height: 1.1;
  color: inherit;
  margin-top: 10px;
  margin-bottom: 10px;
  font-size: 18px;
  font-size: 16px;
  font-weight: 500;
  color: #525252;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 16px 0;
  width: 100%;
  margin: 0;
  cursor: pointer;
}
.header small,
.header .small {
  font-weight: 400;
  line-height: 1;
  color: #777777;
}
.header small,
.header .small {
  font-size: 75%;
}
.header.minimal {
  font-size: 14px;
  line-height: 1.5em;
  font-weight: 300;
  color: #757575;
  justify-content: flex-start;
  padding: 0;
}
.header.bordered {
  padding: 16px;
}
.header.dashboard {
  font-family: inherit;
  font-weight: 500;
  line-height: 1.1;
  color: inherit;
  margin-top: 20px;
  margin-bottom: 10px;
  font-size: 30px;
  font-size: 24px;
  font-weight: 300;
  color: #525252;
  padding: 24px 32px;
  margin: 0;
}
.header.dashboard small,
.header.dashboard .small {
  font-weight: 400;
  line-height: 1;
  color: #777777;
}
.header.dashboard small,
.header.dashboard .small {
  font-size: 65%;
}
.header.solid {
  font-family: inherit;
  line-height: 1.1;
  color: inherit;
  margin-top: 10px;
  margin-bottom: 10px;
  font-size: 14px;
  font-weight: 500;
  color: #525252;
  text-transform: uppercase;
  font-weight: 400;
  padding: 16px;
  margin: 0;
}
.header.solid small,
.header.solid .small {
  font-weight: 400;
  line-height: 1;
  color: #777777;
}
.header.solid small,
.header.solid .small {
  font-size: 75%;
}
.header.reporting {
  padding: 16px;
  margin: 0;
}
